html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

:root {
  --primary-color: #00acdc;
}

body {
  background-color: #fff;
  margin: 0;
  display: block;
}

.logo {
  width: 40px;
  height: auto;
  text-decoration: none;
  display: block;
}

.header {
  text-align: center;
  color: #060b35;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header--container {
  background: #fff;
  padding: 0 50px;
  box-shadow: 0 2px 4px #0000000d;
}

.header:hover {
  border: none;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
}

.nav__link--hover:hover {
  color: var(--primary-color);
}

.nav__link[data-qa="nav-hover"] {
  text-decoration: none;
  position: relative;
}

.nav__link[data-qa="nav-hover"]:hover:after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 25px;
  left: 0;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  color: #060b35;
  text-align: center;
  align-items: center;
  margin: 0 10px;
  text-decoration: none;
  display: flex;
}

.nav__item:first-child {
  margin: 0 10px 0 0;
}

.nav__item:last-child {
  margin: 0 0 0 10px;
}

.nav .is-active {
  color: var(--primary-color);
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.page-body {
  box-sizing: border-box;
  justify-content: center;
  align-items: flex-start;
  min-height: calc(100vh - 50px);
  margin: 0;
  padding: 50px 40px 0;
  display: flex;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition: all .3s;
}

.card__image {
  margin-inline: 19px;
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  display: block;
}

.card__main-text {
  color: #060b35;
  letter-spacing: 0%;
  margin-inline: 16px;
  margin-top: 40px;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__product-code {
  color: #616070;
  letter-spacing: 0%;
  margin-inline: 16px;
  margin-top: 4px;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  margin-inline: 16px;
  justify-content: space-between;
  align-items: center;
  gap: 17px;
  margin-top: 16px;
  margin-bottom: 24px;
  padding: 0;
  display: flex;
}

.card__review-count {
  color: #060b35;
  letter-spacing: 0%;
  z-index: 0;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  position: relative;
}

.card__price {
  letter-spacing: 0%;
  justify-content: space-between;
  align-items: center;
  margin-inline: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__main-text {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.grid {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .grid {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .grid {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .grid {
    --column-count: 4;
  }
}

body {
  margin: 0;
}
